#charity {
  position: relative;
  margin-top: -50px;

  min-height: 2196px;

  color: $primaryColor;

  background: url(../images/charity/charity-bg.jpg), $secondaryColor;
  background-repeat: no-repeat;
  background-position: center;

  border-top-left-radius: 60px;
  border-top-right-radius: 60px;

  background-color: $secondaryColor;

  & .container-full {
    position: relative;

    width: 100%;
    padding-top: 120px;
    padding-bottom: 120px;
    padding-top: 330px;
    padding-bottom: 250px;

    z-index: 6;
  }

  & h2.title-h2 {
    max-width: 760px;
    text-align: center;
    margin: 0 auto;
  }
}

.charity__bg {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
}

.sun-element {
  position: absolute;
  top: 20px;
  left: 50%;

  transform: translateX(-50%);
}

.charity__header {
  margin-top: 60px;
  margin-bottom: 80px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;
  max-width: 630px;
}

.charity__header > * + * {
  margin-top: 40px;
}

.charity__names-wrapper > * + * {
  margin-top: 30px;
}

.name-item {
  padding: 40px;
  color: $thirdColor;
  background-color: $primaryColor;
  border-radius: 60px;

  min-height: 263px;
  width: 49%;

  font-size: 24px;
  line-height: 1.3;

  & span {
    color: $secondaryColor;
    font-size: 36px;
    line-height: 1.05;
  }

  & p {
    font-family: normalFont, sans-serif !important;
    font-weight: 400 !important;
  }
}

.name-item > * + * {
  margin-top: 24px;
}

.charity__names-top,
.charity__names-bottom {
  display: flex;
  justify-content: space-between;
}

.charity__partners {
  margin-top: 80px;
  & h3 {
    text-align: center;
  }
}
