#header {
  background-image: url(../images/header/header-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  //   background-size: cover;
  background-color: #4690ff;

  min-height: 2290px;

  text-align: center;
}

.header__nav {
  padding-top: 40px;
  padding-bottom: 40px;
}

.header__top {
  margin: 0 auto;
  max-width: 812px;
}

.header__wrapper {
  padding-top: 50px;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__link {
  margin: 0 auto;
  margin-top: 40px;

  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 640px;

  & img {
    margin: 0 auto;
  }
}

.header__link a > * + * {
  margin-top: 40px;
}

.header__link > * + * {
  margin-top: 40px;
}

.header__bottom {
  margin: 0 auto;
  max-width: 730px;
  text-align: left;

  & span {
    color: #ffea31;
  }

  & p {
    max-width: 640px;
  }
}

.header__content {
  height: 2050px;

  & .container {
    height: 100%;
  }
}

.header__bottom > * + * {
  margin-top: 30px;
}
