#timeline {
  position: relative;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  min-height: 2700px;

  color: $thirdColor;

  background-image: url(../images/timeline/timeline-bg.png);
  background-repeat: no-repeat;
  background-position: bottom;

  margin-bottom: -252px;
  z-index: 5;

  & .container-full {
    width: 100%;

    padding-bottom: 120px;

    margin-top: -50px;

    z-index: 5;
  }

  & .color-container {
    padding-top: 120px;
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    background-color: $bg-color;
  }

  & .wrapper__container {
    position: relative;
    z-index: 10;
  }
}

.timeline__header {
  max-width: 525px;
  margin-left: auto;
  margin-right: auto;

  & > * + * {
    margin-top: 30px;
  }
}

.timeline__img {
  margin-top: 20px;
}

.timeline__info {
  max-width: 640px;
  margin-top: 60px;

  margin-left: auto;
  margin-right: auto;
}

.timeline__steps {
  margin-top: 120px;

  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
}

.timeline__sequence {
  margin-top: 60px;
}

.timeline__sequence > * + * {
  margin-top: 60px;
}

.item-step__info {
  & span {
    font-weight: 700;
    font-size: 56px;
    line-height: 1.05;

    color: $secondaryColor;
  }

  & p {
    font-weight: 600;
    font-size: 32px;
    line-height: 1.3;
  }
}

.item-step__img {
  flex-shrink: 0;
}

.item-step__info > * + * {
  margin-top: 25px;
}

.item-step {
  display: flex;
}

.item-step > * + * {
  margin-left: 50px;
}

.timeline__bg {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  z-index: 4;
}
